body {
  background: url(https://cdn.mobilesyrup.com/wp-content/uploads/2019/08/code-ninjas.jpg) no-repeat center fixed;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 0px;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  background-color: cornflowerblue;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ol, ul {
  padding-left: 30px;
}

.board.row {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
  text-align: center;
  font-size: 30px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 75px;
  font-weight: bold;
  line-height: 34px;
  height: 120px;
  margin-right:-1px ;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 120px;
}

.square:focus {
  outline: none;
}

.game {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 600px;
}

.game-info {
  margin-left: 20px;
}

.game-board {
  margin: auto;
}

@media only screen and (max-width: 767px) {
  body {
    background: url(https://www.codeninjas.com/assets/img/brands/codeNinjas/cn-bn-out.png) no-repeat fixed;
    display: flex;
    overflow-x: hidden;
    white-space: nowrap;
    padding: 10px;
    width: 300px;
    background-size:  300px 500px;
  }

  .status {
    margin-bottom: 10px;
    text-align: center;
    font-size: 20px;
  }

  .game {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 300px;
  }

  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 75px;
    font-weight: bold;
    line-height: 34px;
    height: 80px;
    margin-right:-1px ;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 80px;
  }
}
